export interface RawCard {
  name: string;
  attack?: number;
  hp?: number;
  cost: number;
  description: string;
  type: 'ally' | 'ability';
  speed: number;
  effect?: RawEffect;
  rules?: string;
  maxHp?: number;
  revealed?: boolean;
  maxAttack?: number;
}

export interface BasicEffectProperties {
  timing: Timing | Timing[];
  target: Targets | Targets[];
  card?: Card;
  amount?: number;
}

export interface DrawEffect extends BasicEffectProperties {
  type: 'draw';
  amount: number;
}

export interface SummonEffect extends BasicEffectProperties {
  type: 'summon';
  card: Card;
}

export interface DamageEffect extends BasicEffectProperties {
  type: 'damage';
  amount: number;
}

export interface HealingEffect extends BasicEffectProperties {
  type: 'healing';
  amount: number;
}

export interface WeakeningEffect extends BasicEffectProperties {
  type: 'weaken';
  amount: number;
}

export interface StrengtheningEffect extends BasicEffectProperties {
  type: 'strengthen';
  amount: number;
}

export type Targets =
  | 'player'
  | 'opponent'
  | 'self'
  | 'playerAllies'
  | 'oppAllies'
  | 'sameRow'
  | 'friendlyUpRow'
  | 'friendlyDownRow'
  | 'enemyUpRow'
  | 'enemyDownRow'
  | 'otherPlayerAllies';

export type Timing =
  | 'play'
  | 'reveal'
  | 'combat'
  | 'attack'
  | 'kill'
  | 'death'
  | 'eot';

export type RawEffect =
  | DrawEffect
  | SummonEffect
  | DamageEffect
  | HealingEffect
  | WeakeningEffect
  | StrengtheningEffect;
