import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

import { Card } from '../shared/card/card.model';
// import { Effect } from '../shared/effect.model'

import { DeckService } from '../player/deck/deck.service';
import { EffectsService } from '../shared/effects.service';

@Injectable({ providedIn: 'root' })
export class BattlefieldService {
  private melee: Card | null;
  private ranged: Card | null;
  private mental: Card | null;
  private meleeOpp: Card | null;
  private rangedOpp: Card | null;
  private mentalOpp: Card | null;

  private playerRows: string[] = ['mental', 'ranged', 'melee'];
  private oppRows: string[] = ['mentalOpp', 'rangedOpp', 'meleeOpp'];

  cardEmitter = new Subject<{ card: Card | null; row: string }>();

  constructor(
    private deckService: DeckService,
    private effectsService: EffectsService
  ) {}

  getCard(row: string) {
    return this[row];
  }
  setPlayerCard(card: Card, row: string) {
    this[row] = card;
    this.cardEmitter.next({ card: card, row: row });
    if (card.effect && card.effect.timing === 'play') {
      this.applyBattlefiedEffect(card, row);
      console.log(
        `Gracz wstawia ${this[row].name} w ${row} z efektem ${this[row].effect.type} na ${this[row].effect.target}`
      );
    } else {
      console.log(`Gracz wstawia ${this[row].name} w ${row}`);
    }
  }
  setOppCard(card: Card, row: string) {
    this[row + 'Opp'] = card;
    this.cardEmitter.next({ card: card, row: row + 'Opp' });
    console.log(
      `Przeciwnik wstawia ${this[row + 'Opp'].name} w ${row + 'Opp'}`
    );
  }
  changeCardHp(number: number, row: string) {
    this[row].hp + number > this[row].maxHp
      ? (this[row].hp = this[row].maxHp)
      : (this[row].hp += number);
    number < 0
      ? console.log(`${this[row].name} w ${row} otrzymuje ${number} obrażeń`)
      : console.log(`${this[row].name} w ${row} jest leczony o ${number}`);
  }
  changeCardAttack(number: number, row: string) {
    this[row].attack + number < 0 ? this[row].attack = 0 : (this[row].attack += number);
    number < 0
      ? console.log(`${this[row].name} w ${row} otrzymuje ${number} obrażeń`)
      : console.log(`${this[row].name} w ${row} jest leczony o ${number}`);
  }
  removeCard(card: Card, row: string) {
    this.deckService.discard(card);
    console.log(this[row].name + ' umiera w ' + row);
    this[row] = null;
    this.cardEmitter.next({ card: null, row: row });
    if (card.effect && card.effect.timing === 'death') {
      this.applyBattlefiedEffect(card, row);
      console.log(`z efektem ${card.effect.type} na ${card.effect.target}`);
    }
  }
  buryTheFallen() {
    const rows = [
      'mental',
      'ranged',
      'melee',
      'mentalOpp',
      'rangedOpp',
      'meleeOpp',
    ];
    rows.forEach((row) => {
      if (this[row] && this[row].hp <= 0) {
        this.removeCard(this[row], row);
      }
    });
  }
  markAsRevealed(row: string) {
    this[row].revealed = true
    console.log(this[row].name + "ma status revealed");
  }
  calculateTargets(row: string, target: string | string[]): string[] {
    let targets: string[] = [];
    let playerRows = ['mental', 'ranged', 'melee'];
    let oppRows = ['mentalOpp', 'rangedOpp', 'meleeOpp'];
    if (row.includes('Opp')) {
      switch (target) {
        case 'player':
          targets.push('opponent');
          break;
        case 'opponent':
          targets.push('player');
          break;
        case 'self':
          targets.push(row);
          break;
        case 'playerAllies':
          targets = targets.concat(oppRows);
          break;
        case 'oppAllies':
          targets = targets.concat(playerRows);
          break;
        case 'sameRow':
          targets.push(row.substring(0, row.length - 3));
          break;
        case 'friendlyUpRow':
          oppRows.indexOf(row) - 1 >= 0
            ? targets.push(oppRows[oppRows.indexOf(row) - 1])
            : null;
          break;
        case 'friendlyDownRow':
          oppRows.indexOf(row) + 1 <= 2
            ? targets.push(oppRows[oppRows.indexOf(row) + 1])
            : null;
          break;
        case 'enemyUpRow':
          playerRows.indexOf(row.substring(0, row.length - 3)) - 1 >= 0
            ? targets.push(
                playerRows[
                  playerRows.indexOf(row.substring(0, row.length - 3)) - 1
                ]
              )
            : null;
          break;
        case 'enemyDownRow':
          playerRows.indexOf(row.substring(0, row.length - 3)) + 1 <= 2
            ? targets.push(
                playerRows[
                  playerRows.indexOf(row.substring(0, row.length - 3)) + 1
                ]
              )
            : null;
          break;
        case 'otherPlayerAllies':
          targets = targets.concat(oppRows.splice(oppRows.indexOf(row), 1));
          break;
      }
    } else {
      switch (target) {
        case 'player':
          targets.push(target);
          break;
        case 'opponent':
          targets.push(target);
          break;
        case 'self':
          targets.push(row);
          break;
        case 'playerAllies':
          targets = targets.concat(playerRows);
          break;
        case 'oppAllies':
          targets = targets.concat(oppRows);
          break;
        case 'sameRow':
          targets.push(row + 'Opp');
          break;
        case 'friendlyUpRow':
          playerRows.indexOf(row) - 1 >= 0
            ? targets.push(playerRows[playerRows.indexOf(row) - 1])
            : null;
          break;
        case 'friendlyDownRow':
          playerRows.indexOf(row) + 1 <= 2
            ? targets.push(playerRows[playerRows.indexOf(row) + 1])
            : null;
          break;
        case 'enemyUpRow':
          oppRows.indexOf(row + 'Opp') - 1 >= 0
            ? targets.push(oppRows[oppRows.indexOf(row + 'Opp') - 1])
            : null;
          break;
        case 'enemyDownRow':
          oppRows.indexOf(row + 'Opp') + 1 <= 2
            ? targets.push(oppRows[oppRows.indexOf(row + 'Opp') + 1])
            : null;
          break;
        case 'otherPlayerAllies':
          targets = targets.concat(playerRows.splice(playerRows.indexOf(row), 1));
          break;
      }
    }
    return targets;
  }
  applyBattlefiedEffect(card: Card, row: string) {
    switch (card.effect.type) {
      case 'draw':
        row.includes("Opp") ? null : this.effectsService.applyEffect(card.effect);
        break;
      case 'healing':
        let healingTargets: string[] = Array.isArray(card.effect.target)
          ? card.effect.target.reduce(
              (allTargets, currentTarget) =>
                allTargets = allTargets.concat(this.calculateTargets(row, currentTarget)),
              []
            )
          : this.calculateTargets(row, card.effect.target);
          console.log(`Cele zdolności to ${healingTargets}`)
        healingTargets.forEach((target) =>
          target === 'player' || target === 'opponent'
            ? this.effectsService.applyEffect(card.effect, target)
            : this[target] ? this.changeCardHp(card.effect.amount, target) : null
        );
        break;
      case 'damage':
        let damageTargets: string[] = Array.isArray(card.effect.target)
          ? card.effect.target.reduce(
              (allTargets, currentTarget) =>
                allTargets = allTargets.concat(this.calculateTargets(row, currentTarget)),
              []
            )
          : this.calculateTargets(row, card.effect.target);
          console.log(`Cele zdolności to ${damageTargets}`)
        damageTargets.forEach((target) =>
          target === 'player' || target === 'opponent'
            ? this.effectsService.applyEffect(card.effect, target)
            : this[target] ? this.changeCardHp(-card.effect.amount, target) : null
        );
        break;
      case 'weaken':
        let weakenTargets: string[] = Array.isArray(card.effect.target)
          ? card.effect.target.reduce(
              (allTargets, currentTarget) =>
                allTargets = allTargets.concat(this.calculateTargets(row, currentTarget)),
              []
            )
          : this.calculateTargets(row, card.effect.target);
        console.log(`Cele zdolności to ${weakenTargets}`);
        weakenTargets.forEach((target) => this[target] ? this.changeCardAttack(-card.effect.amount, target) : null);
        break;
        case 'strengthen':
          let strengthenTargets: string[] = Array.isArray(card.effect.target)
            ? card.effect.target.reduce(
                (allTargets, currentTarget) =>
                  allTargets = allTargets.concat(this.calculateTargets(row, currentTarget)),
                []
              )
            : this.calculateTargets(row, card.effect.target);
          console.log(`Cele zdolności to ${strengthenTargets}`);
          strengthenTargets.forEach(
            (target) => this[target] ? this.changeCardAttack(card.effect.amount, target) : null);
          break;
      case 'summon':
        let summonTargets: string[] = Array.isArray(card.effect.target)
          ? card.effect.target.reduce(
              (allTargets, currentTarget) =>
                allTargets = allTargets.concat(this.calculateTargets(row, currentTarget)),
              []
            )
          : this.calculateTargets(row, card.effect.target);
          console.log(`Cele zdolności to ${summonTargets}`)
        summonTargets.forEach((target) =>
          !this[target]
            ? this.playerRows.includes(target)
              ? this.setPlayerCard(new Card(card.effect.card), target)
              : this.setOppCard(
                  new Card(card.effect.card),
                  target.substring(0, target.length - 3)
                )
            : null
        );
        break;
      default:
        break;
    }
  }
}
