import { Injectable } from '@angular/core';
import { Effect } from '../shared/effect.model';
// import { Subject } from 'rxjs'
// import { cards } from '../data/cards'

// import { DeckService } from "../player/deck/deck.service"
import { HandService } from '../player/hand/hand.service';
import { PlayerService } from '../player/player.service';
import { OpponentService } from '../opponent/opponent.service';

// import { Card } from "../shared/card/card.model"

@Injectable({ providedIn: 'root' })
export class EffectsService {
  constructor(
    private handService: HandService,
    private playerService: PlayerService,
    private opponentService: OpponentService
  ) {}

  applyEffect(effect: Effect, target: string = '') {
    switch (effect.type) {
      case 'draw':
        this.handService.drawCards(effect.amount);
        break;
      case 'healing':
        target === 'player'
          ? this.playerService.changeLifePoints(effect.amount)
          : this.opponentService.changeOppHp(effect.amount);
        break;
      case 'damage':
        target === 'player'
          ? this.playerService.changeLifePoints(-effect.amount)
          : this.opponentService.changeOppHp(-effect.amount);
        break;
      default:
        break;
    }
  }
}
