import { Injectable } from '@angular/core';
import { Subject } from 'rxjs'

import { Card } from '../../shared/card/card.model';
import { deck } from "../../data/cards"

@Injectable({providedIn: 'root'})

export class DeckService {
  private deck: Card[] = deck.map(card => new Card(card));
  private discardPile: Card[] = []

  deckSizeSubject = new Subject<number>()
  constructor() {}

  getDeck() {
    return this.deck.slice();
  }
  getNumberOfCards() {
    return this.deck.length
  }
  shuffle() {
    var currentIndex = this.deck.length,
      temporaryValue: Card,
      randomIndex: number;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;

      // And swap it with the current element.
      temporaryValue = this.deck[currentIndex];
      this.deck[currentIndex] = this.deck[randomIndex];
      this.deck[randomIndex] = temporaryValue;
    }
  }
  draw(number: number) {
    this.deckSizeSubject.next(this.getNumberOfCards() - number)
    return this.deck.splice(0, number)
  }
  discard(card: Card) {
    this.discardPile.push(card)
  }
}
