import { Component } from '@angular/core';

import { Card } from './shared/card/card.model';

import { CombatService } from './shared/combat.service';
import { TurnService } from './shared/turn.service';
import { BattlefieldService } from './battlefield/battlefield.service';
import { HttpService } from './shared/http.service';
import { PlayerService } from './player/player.service'
import { HandService } from './player/hand/hand.service'
import { DeckService } from './player/deck/deck.service'

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent {
  title = 'taern-card';
  isPlayerNumberSet: boolean = false;
  isDataSent: boolean = false;
  isDataReceived: boolean = false;
  isTurnNumberCorrect: boolean = false;

  turnNumber: number = 1;
  playerNumber: number;

  constructor(
    private combatService: CombatService,
    private turnService: TurnService,
    private battlefieldService: BattlefieldService,
    private httpService: HttpService,
    private playerService: PlayerService,
    private handService: HandService,
    private deckService: DeckService
  ) {
    this.turnService.turnSubject.subscribe(turnNumber => this.turnNumber = turnNumber)
  }

  combat() {
    this.combatService.commenceCombat();
    this.isDataSent = false;
    this.isDataReceived = false;
  }
  setPlayerNumber(number: number) {
    this.playerService.setPlayerNumber(number);
    this.isPlayerNumberSet = true;
    this.playerNumber = number;
  }
  sendData() {
    this.httpService
      .sendData(
        {
          melee: this.battlefieldService.getCard('melee')
            ? this.battlefieldService.getCard('melee')
            : '',
          ranged: this.battlefieldService.getCard('ranged')
            ? this.battlefieldService.getCard('ranged')
            : '',
          mental: this.battlefieldService.getCard('mental')
            ? this.battlefieldService.getCard('mental')
            : '',
          turn: this.turnNumber,
          hand: this.handService.getHandSize(),
          deck: this.deckService.getNumberOfCards()
        },
        this.playerService.getPlayerNumber()
      )
      .subscribe((response) => (this.isDataSent = true));
  }
  getData() {
    this.httpService
      .getData(this.playerService.getPlayerNumber() === 1 ? 2 : 1)
      .subscribe((response) => {
        if (response && response.turn === this.turnNumber) {
          response.melee
            ? this.battlefieldService.setOppCard(
                new Card(response.melee),
                'melee'
              )
            : null;
          response.ranged
            ? this.battlefieldService.setOppCard(
                new Card(response.ranged),
                'ranged'
              )
            : null;
          response.mental
            ? this.battlefieldService.setOppCard(
                new Card(response.mental),
                'mental'
              )
            : null;
          this.isDataReceived = true;
        }
      });
  }
}
